import React from 'react';
import useIsMobile from 'hooks/useIsMobile';
import EntrySection from 'sections/EntrySection';
import TrafficSection from 'sections/TrafficSection';
import LazypackSection from 'sections/LazypackSection';
import AttractionSection from 'sections/AttractionSection';
import RankSection from 'sections/RankSection';
import ModernSection from 'sections/ModernSection';
import TicketSection from 'sections/TicketSection';
import TextCarousel from 'components/TextCarousel';
import Footer from 'components/Footer';
import MobileMenu from 'components/MobileMenu';
import NavigationBar from 'components/NavigationBar';
import MobileNavigationBar from 'components/MobileNavigationBar';
import { NAVIGATION_BAR } from 'constants/index';
import GlobalStyle from './GlobalStyle';
import { ContentBlock, MenuButton, LazypackAttractionBlock } from './styled';

const App = () => {
  const [isNavigationShow, setIsNavigationShow] = React.useState(false);
  const [isMobileMenuShow, setIsMobileMenuShow] = React.useState(false);

  const isMobile = useIsMobile();

  const handleMenuButtonClick = () => {
    setIsMobileMenuShow(true);
  };

  const handleMenuCloseButtonClick = () => {
    setIsMobileMenuShow(false);
  };

  React.useEffect(() => {
    if (!isMobile) {
      const rootElement = document.getElementById('root');

      const handleScroll = () => {
        if (rootElement.scrollTop >= NAVIGATION_BAR.DESKTOP.HEIGHT) {
          setIsNavigationShow(true);
        } else {
          setIsNavigationShow(false);
        }
      };

      handleScroll();

      rootElement.addEventListener('scroll', handleScroll);

      return () => {
        rootElement.removeEventListener('scroll', handleScroll);
      };
    }
  }, [isMobile]);

  return (
    <>
      {/* 全域樣式 */}
      <GlobalStyle />

      {/* Desktop 導覽列 */}
      {!isMobile && isNavigationShow && <NavigationBar />}

      {/* Mobile 導覽列 */}
      {isMobile && isNavigationShow && <MobileNavigationBar />}

      {/* Mobile 選單 */}
      {isMobile && <MenuButton onClick={handleMenuButtonClick} />}

      {isMobile && isMobileMenuShow && (
        <MobileMenu onClose={handleMenuCloseButtonClick} />
      )}

      {/* 文字跑馬燈 */}
      {!isMobile && <TextCarousel />}

      {/* 頁首 */}
      <EntrySection />

      {/* 內容 */}
      <ContentBlock>
        {/* 交通資訊 */}
        <TrafficSection />

        {/* 懶人包 | 踩點推薦 */}
        <LazypackAttractionBlock>
          {/* 懶人包 */}
          <LazypackSection />

          {/* 踩點推薦 */}
          <AttractionSection />
        </LazypackAttractionBlock>

        {/* 打卡排行榜 */}
        <RankSection />

        {/* 摩登市集 */}
        <ModernSection />

        {/* 票券 */}
        <TicketSection />

        {/* 頁尾 */}
        <Footer />
      </ContentBlock>
    </>
  );
};

export default App;
