import React from 'react';
import { Autoplay, EffectCreative } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import useIsMobile from 'hooks/useIsMobile';
import { SECTION_ID, ENTRY_IMAGE_LIST } from 'constants/index';
import {
  Container,
  BottomBlock,
  TextImage,
  CarouselImage,
  TextImageWrapper,
  CarouselImageWrapper,
  BuddhaImage,
  BuddhaImageWrapper,
  BottomBlockWrapper,
  EntryImage1,
  EntryImage2,
  EntryImage3,
  EntryImage4,
  EntryImage5,
} from './styled';

const TopSection = () => {
  const isMobile = useIsMobile();

  const imageList = isMobile
    ? ENTRY_IMAGE_LIST.MOBILE
    : ENTRY_IMAGE_LIST.DESKTOP;

  return (
    <Container id={SECTION_ID.ENTRY}>
      {/* 圖片輪播 */}
      <Swiper
        loop
        noSwiping
        centeredSlides
        autoHeight
        spaceBetween={0}
        noSwipingClass="swiper-slide"
        autoplay={{ delay: 3000 }}
        slidesPerView={isMobile ? 1.5 : 2}
        modules={[Autoplay, EffectCreative]}
        effect="creative"
        creativeEffect={{
          limitProgress: 2,
          prev: {
            scale: 0.8,
            translate: ['-95%', 0, 0],
          },
          next: {
            scale: 0.8,
            translate: ['95%', 0, 0],
          },
        }}
      >
        {imageList.map((image, index) => (
          <SwiperSlide key={`slide-image-${index}`}>
            <CarouselImageWrapper>
              <CarouselImage src={image} />
            </CarouselImageWrapper>
          </SwiperSlide>
        ))}

        {/* 插圖 */}
        <EntryImage1 />
        <EntryImage2 />
        <EntryImage3 />
        <EntryImage4 />
        <EntryImage5 />

        {!isMobile && (
          <EntryImage4 style={{ left: 'unset', right: '0', zIndex: 7 }} />
        )}
      </Swiper>

      {/* 下方圖片區塊 */}
      <BottomBlock>
        <BottomBlockWrapper>
          <TextImageWrapper>
            <TextImage />
          </TextImageWrapper>

          <BuddhaImageWrapper>
            <BuddhaImage />
            {isMobile ? (
              <EntryImage4
                style={{
                  top: '50%',
                  left: -92,
                  width: 200,
                  transform: 'rotate(90deg)',
                }}
              />
            ) : (
              <EntryImage4
                style={{
                  top: 'unset',
                  left: 'unset',
                  bottom: '-6vw',
                  right: '-12vw',
                  zIndex: 7,
                }}
              />
            )}
          </BuddhaImageWrapper>
        </BottomBlockWrapper>
      </BottomBlock>
    </Container>
  );
};

export default TopSection;
