import React from 'react';
import ParagraphCard from 'components/ParagraphCard';
import MoreButton from 'components/MoreButton';
import ButtonSelector from 'components/ButtonSelector';
import VideoCard from 'components/VideoCard';
import {
  Container,
  Wrapper,
  Title,
  TinyText,
  Description,
  TopBlock,
  BottomBlock,
  VideoBlock,
  InfoBlock,
} from 'components/LazypackAttraction/styled';
import { SECTION_ID, VIDEO_LIST, PARAGRAPH_LIST } from 'constants/index';
import { FormBlock } from './styled';

const AttractionSection = () => {
  // 選取的標籤
  const [selectedTag, setSelectedTag] = React.useState('');

  const handleSelectorClick = tag => () => {
    setSelectedTag(tag);
  };

  return (
    <Container
      id={SECTION_ID.ATTRACTION}
      style={{ backgroundColor: '#fff2b7' }}
    >
      <Wrapper>
        <TopBlock>
          <InfoBlock>
            <TinyText>A reas</TinyText>
            <Title>踩點推薦</Title>
            <Description>來看看推薦景點吧!!</Description>
          </InfoBlock>

          <FormBlock>
            <ButtonSelector
              selected={selectedTag === 'hotel'}
              onClick={handleSelectorClick('hotel')}
            >
              HOTEL 飯店
            </ButtonSelector>
            <ButtonSelector
              selected={selectedTag === 'food'}
              onClick={handleSelectorClick('food')}
            >
              FOOD 美食
            </ButtonSelector>
            <ButtonSelector
              selected={selectedTag === 'spot'}
              onClick={handleSelectorClick('spot')}
            >
              SPOT 景點
            </ButtonSelector>
          </FormBlock>
        </TopBlock>

        <BottomBlock>
          <VideoBlock>
            {VIDEO_LIST.map(video => (
              <VideoCard key={video.id} image={video.image} video={video.url} />
            ))}
          </VideoBlock>

          <MoreButton>看更多影片</MoreButton>

          {PARAGRAPH_LIST.map(paragraph => (
            <ParagraphCard
              key={paragraph.id}
              tag={paragraph.tag}
              link={paragraph.link}
              title={paragraph.title}
              image={paragraph.image}
            />
          ))}
          <MoreButton style={{ marginBottom: 0 }}>看更多文章</MoreButton>
        </BottomBlock>
      </Wrapper>
    </Container>
  );
};

export default AttractionSection;
