import React from 'react';
import { GIRLSTALK_URL, NAVIGATION_LIST } from 'constants/index';
import Link from 'components/Link';
import OutsideLink from 'components/OutsideLink';
import { Container, Seperator, LeftBlock, RightBlock, Logo } from './styled';

const NavigationBar = () => {
  return (
    <Container>
      <LeftBlock>
        <OutsideLink href={GIRLSTALK_URL}>
          <Logo />
        </OutsideLink>
      </LeftBlock>

      <RightBlock>
        {NAVIGATION_LIST.map((navigation, index) => (
          <React.Fragment key={navigation.id}>
            <Link
              key={navigation.id}
              href={`#${navigation.id}`}
              style={{ width: 'auto' }}
            >
              {navigation.title}
            </Link>
            {index + 1 !== NAVIGATION_LIST.length && <Seperator />}
          </React.Fragment>
        ))}
      </RightBlock>
    </Container>
  );
};

export default NavigationBar;
