import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import useIsMobile from 'hooks/useIsMobile';
import TicketCard from 'components/TicketCard';
import { SECTION_ID, TICKET_LIST } from 'constants/index';
import {
  Container,
  Wrapper,
  TopBlock,
  BottomBlock,
  TopLeftTextImage,
  TopRightText,
  CarouselPrevIcon,
  CarouselNextIcon,
} from './styled';

const TicketSection = () => {
  const sliderRef = React.useRef(null);

  const isMobile = useIsMobile();

  const handlePrev = React.useCallback(() => {
    if (!sliderRef.current) return;
    sliderRef.current.swiper.slidePrev();
  }, []);

  const handleNext = React.useCallback(() => {
    if (!sliderRef.current) return;
    sliderRef.current.swiper.slideNext();
  }, []);

  return (
    <Container id={SECTION_ID.TICKET}>
      <Wrapper>
        <TopBlock>
          <TopLeftTextImage />
          <TopRightText>好吃.好逛.好好買</TopRightText>
        </TopBlock>

        <BottomBlock>
          <Swiper
            loop
            centeredSlides
            ref={sliderRef}
            spaceBetween={40}
            slidesPerView={isMobile ? 1 : 3}
          >
            {TICKET_LIST.map(ticket => (
              <SwiperSlide key={ticket.id}>
                <TicketCard
                  link={ticket.link}
                  title={ticket.title}
                  image={ticket.image}
                  price={ticket.price}
                  recommand={ticket.recommand}
                  description={ticket.description}
                />
              </SwiperSlide>
            ))}
          </Swiper>
          <CarouselPrevIcon onClick={handlePrev} />
          <CarouselNextIcon onClick={handleNext} />
        </BottomBlock>
      </Wrapper>
    </Container>
  );
};

export default TicketSection;
