import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import useIsMobile from 'hooks/useIsMobile';
import OutsideLink from 'components/OutsideLink';
import { SECTION_ID, MODERN_IMAGE_LIST } from 'constants/index';
import {
  Container,
  Wrapper,
  ModernTopImage,
  ContentBlock,
  ParagraphBlock,
  InnerWrapper,
  Paragraph,
  ModernBottomImage,
  ModernBottomImageWrapper,
  CarouselPrevIcon,
  CarouselNextIcon,
  Image,
  SwiperWrapper,
} from './styled';

const ModernSection = () => {
  const isMobile = useIsMobile();

  const sliderRef = React.useRef(null);

  const handlePrev = React.useCallback(() => {
    if (!sliderRef.current) return;
    sliderRef.current.swiper.slidePrev();
  }, []);

  const handleNext = React.useCallback(() => {
    if (!sliderRef.current) return;
    sliderRef.current.swiper.slideNext();
  }, []);

  return (
    <Container id={SECTION_ID.MODERN}>
      {/* 摩登市集視覺圖 */}
      <ModernTopImage isMobile={isMobile} />

      <Wrapper>
        <InnerWrapper>
          <ContentBlock>
            <ModernBottomImageWrapper>
              <ModernBottomImage />
            </ModernBottomImageWrapper>

            <ParagraphBlock>
              <Paragraph style={{ marginBottom: 36 }}>
                彰化為日治時期商業重鎮，擁有許多糕餅名店，本次『百年の店
                摩登珠寶盒』邀請彰化在地知名糕餅店，以珠寶盒概念集結各家糕餅的經典口味，打造一款『百年の店
                摩登珠寶盒』單盒售價$990，極限量發售！
              </Paragraph>
              <Paragraph>
                百年の店
                摩登珠寶盒內含：林記蛋黃酥2顆、不二家蛋黃酥2顆、XXXX、XXXX
              </Paragraph>
            </ParagraphBlock>
          </ContentBlock>

          <SwiperWrapper>
            <Swiper
              loop
              centeredSlides
              ref={sliderRef}
              spaceBetween={40}
              slidesPerView={isMobile ? 1 : 3}
            >
              {MODERN_IMAGE_LIST.map(modernImage => (
                <SwiperSlide key={modernImage.id}>
                  <OutsideLink href={modernImage.link}>
                    <Image src={modernImage.image} />
                  </OutsideLink>
                </SwiperSlide>
              ))}
            </Swiper>
            <CarouselPrevIcon onClick={handlePrev} />
            <CarouselNextIcon onClick={handleNext} />
          </SwiperWrapper>
        </InnerWrapper>
      </Wrapper>
    </Container>
  );
};

export default ModernSection;
