import React from 'react';
import OutsideLink from 'components/OutsideLink';
import { GIRLSTALK_URL, PLATFORM_LIST } from 'constants/index';
import {
  Container,
  Wrapper,
  LeftBlock,
  MiddleBlock,
  RightBlock,
  Title,
  Logo,
  Copyright,
  PlatformBlock,
  Icon,
  MobileCopyright,
} from './styled';

const Footer = () => {
  const currentYear = new Date().getFullYear();
  const copyright = `${currentYear} GIRLSTALK © All Rights Reserved.`;

  return (
    <Container>
      <Wrapper>
        <LeftBlock>
          <Title>主辦單位</Title>
          <OutsideLink href={GIRLSTALK_URL}>
            <Logo />
          </OutsideLink>
        </LeftBlock>
        <MiddleBlock>
          <Title>協辦單位</Title>
        </MiddleBlock>
        <RightBlock>
          <Title>追蹤最新消息</Title>
          <PlatformBlock>
            {PLATFORM_LIST.map(platform => (
              <OutsideLink
                key={platform.id}
                href={platform.link}
                style={{ width: 'auto' }}
              >
                <Icon src={platform.icon} />
              </OutsideLink>
            ))}
          </PlatformBlock>
          <Copyright>{copyright}</Copyright>
        </RightBlock>

        <MobileCopyright>{copyright}</MobileCopyright>
      </Wrapper>
    </Container>
  );
};

export default Footer;
