import React from 'react';
import { Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import useIsMobile from 'hooks/useIsMobile';
import RankCard from 'components/RankCard';
import { SECTION_ID, RANK_IMAGE_LIST, RANK_LIST } from 'constants/index';
import {
  Container,
  Wrapper,
  LeftBlock,
  Title,
  SubTitle,
  PinBlock,
  PinIcon,
  PinText,
  RankList,
  RankListItem,
  Rank,
  Spot,
  BuddhaImage,
  RankListBlock,
  BuddhaImageBlock,
  TitleBlock,
} from './styled';

const RankSection = () => {
  const isMobile = useIsMobile();

  return (
    <Container id={SECTION_ID.RANK}>
      <Wrapper>
        <LeftBlock>
          <TitleBlock>
            <Title>
              給我一點靈感
              <br />
              看看大家都去哪裡玩？
            </Title>
            <SubTitle>Where can I travel to….？</SubTitle>
            <PinBlock>
              <PinIcon />
              <PinText>
                Instagram 最夯！
                <br />
                打卡排行榜 Ranking
              </PinText>
            </PinBlock>
          </TitleBlock>

          <RankListBlock>
            <RankList>
              {RANK_LIST.map((rank, index) => (
                <RankListItem key={rank.id}>
                  <Rank>Top {index + 1}</Rank>
                  <Spot>{rank.id}</Spot>
                </RankListItem>
              ))}
            </RankList>

            <BuddhaImageBlock>
              <BuddhaImage />
            </BuddhaImageBlock>
          </RankListBlock>
        </LeftBlock>

        <Swiper
          loop
          noSwiping
          noSwipingClass="swiper-slide"
          spaceBetween={36}
          autoplay={{ delay: 3000 }}
          slidesPerView={isMobile ? 1 : 2}
          modules={[Autoplay]}
        >
          {RANK_IMAGE_LIST.map(image => (
            <SwiperSlide key={image.id}>
              <RankCard image={image.url} />
            </SwiperSlide>
          ))}
        </Swiper>
      </Wrapper>
    </Container>
  );
};

export default RankSection;
