import React from 'react';
import OutsideLink from 'components/OutsideLink';
import { GIRLSTALK_URL, NAVIGATION_LIST } from 'constants/index';
import {
  Container,
  Wrapper,
  TopBlock,
  MiddleBlock,
  BottomBlock,
  LeftTopText,
  Logo,
  CloseButton,
  StyledLink,
  Seperator,
  LinkBlock,
} from './styled';

const MobileMenu = ({ onClose }) => {
  return (
    <Container>
      <Wrapper>
        <TopBlock>
          <LeftTopText />
          <CloseButton onClick={onClose} />
        </TopBlock>

        <MiddleBlock>
          <LinkBlock>
            {NAVIGATION_LIST.map((navigation, index) => (
              <React.Fragment key={navigation.id}>
                <StyledLink href={`#${navigation.id}`} onClick={onClose}>
                  {navigation.title}
                </StyledLink>
                {index + 1 < NAVIGATION_LIST.length && <Seperator />}
              </React.Fragment>
            ))}
          </LinkBlock>
        </MiddleBlock>

        <BottomBlock>
          <OutsideLink href={GIRLSTALK_URL}>
            <Logo />
          </OutsideLink>
        </BottomBlock>
      </Wrapper>
    </Container>
  );
};

export default MobileMenu;
